.clear {
  clear: both;
}

.fa-instagram {
  color: #cf153d;
}

.fa-linkedin {
  color: #1556cf;
}

.fa-facebook {
  color: #2815cf;
}

.fa-snapchat {
  color: #ecde16;
}

.profile-header-avatar {
  border: 5px solid #fff;
  border-radius: 100%;
  object-fit: cover;
  background-color: #fff;
  width:250px;
  height:250px;
}

.profile-header {
  align-items: center;
  border-radius: 10px;
  background: #F6F6F6;
  box-shadow: 4px 4px 70px rgba(0, 0, 0, 0.2);
  padding: 45px;
  margin-top: 45px;
}

.profile-wrapper {
  border-radius: 10px;
  background: #F6F6F6;
  box-shadow: 4px 4px 70px rgba(0, 0, 0, 0.2);
  padding: 45px;
  margin-top: 45px;


}

.profile-points {
  text-align: center;
  margin: auto;
  height: 80px;
}

.points {
  font-size: 20px;
  font-weight: 400;
}

.point-label {
  color: rgba(100, 100, 100, 0.5);
  line-height: 15px;
  letter-spacing: .3px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  margin-top: 25%;
}

@media screen and (max-width: 991px) {

  .profile-header,
  .profile-wrapper {
    margin-top: 15px;
  }
  .media-body {
    margin-top: -10px;
  }
}


.lazy-img {
    text-align: center;
    font-size: 10px;
}

@media screen and (max-width: 400px) {
  .profile-header-avatar {
    width : 175px;
    height : 175px;
  }

}

.media-body {
  font-size: 32px;
}


